<template>
    <div>
    <h3>Novo Contato</h3>
        <div id="preencher" class="row">
            <form @submit.prevent="guardarContato" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="contato_id" id="contato_id" required>
                        <label for="contato_id">ID de utilizador</label>
                    </div>
                </div>  
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="primeiroNome" id="primeiroNome" required>
                        <label for="primeiroNome">Primeiro Nome</label>
                    </div>
                </div>  
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="ultimoNome" id="ultimoNome" required>
                        <label for="ultimoNome">Ultimo Nome</label>
                    </div>
                </div>  
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="morada" id="morada" required>
                        <label for="morada">Morada</label>
                    </div>
                </div>  
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="telemovel" id="telemovel" required>
                        <label for="telemovel">Telemóvel</label>
                    </div>
                </div>  
                <button type="submit" class="btn">Submeter</button>
                <router-link style="background-color: grey; color: white;" to="/cont" class="btn grey">Retroceder</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import firebase from './firebaseInit'
export default {
  name: 'NovoContato',
  data () {
    return {
        contato_id : null,
        primeiroNome : null,
        ultimoNome : null,
        morada : null,
        telemovel : null
    }
  },
  methods: {
      guardarContato () {
          firebase.collection('contatos').add({
            contato_id: this.contato_id,
            primeiroNome : this.primeiroNome,
            ultimoNome : this.ultimoNome,
            morada : this.morada,
            telemovel : this.telemovel
          })
          .then(docRef => {
              this.$router.push('/cont')
              .catch(error => console.log(err))
          })
          
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



