<template>
    <div id="editar-contato">
    <h3>Editar Contato</h3>
        <div id="preencher" class="row">
            <form @submit.prevent="editarContato" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="contato_id" id="contato_id" required>
                        <label for="contato_id" class="active">ID de utilizador</label>
                    </div>
                </div>  
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="primeiroNome" id="primeiroNome" required>
                        <label for="primeiroNome" class="active">Primeiro Nome</label>
                    </div>
                </div>  
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="ultimoNome" id="ultimoNome" required>
                        <label for="ultimoNome" class="active">Ultimo Nome</label>
                    </div>
                </div>  
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="morada" id="morada" required>
                        <label for="morada" class="active">Morada</label>
                    </div>
                </div>  
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="telemovel" id="telemovel" required>
                        <label for="telemovel" class="active">Telemóvel</label>
                    </div>
                </div>  
                <button type="submit" class="btn">Submeter</button>
                <router-link to="/cont" class="btn grey">Retroceder</router-link>
            </form>
        </div>
    </div>
</template>
<script>
import firebase from './firebaseInit'
export default {
  name: "editar-contato",
  data() {
    return {
      contato_id: null,
      primeiroNome: null,
      ultimoNome: null,
      morada: null,
      telemovel: null
    };
  },
  beforeRouteEnter(to, from, next) {
    firebase
      .collection("contatos")
      .where("contato_id", "==", to.params.contato_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(elem => {
          next(vm => {
            vm.contato_id = elem.data().contato_id;
            vm.primeiroNome = elem.data().primeiroNome;
            vm.ultimoNome = elem.data().ultimoNome;
            vm.morada = elem.data().morada;
            vm.telemovel = elem.data().telemovel;
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      firebase
        .collection("contatos")
        .where("contato_id", "==", this.$route.params.contato_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(elem => {
            this.contato_id = elem.data().contato_id;
            this.primeiroNome = elem.data().primeiroNome;
            this.ultimoNome = elem.data().ultimoNome;
            this.morada = elem.data().morada;
            this.telemovel = elem.data().telemovel;
            console.log(this.contato_id);
          })
        })
    },
    editarContato () {
        firebase
        .collection("contatos")
        .where("contato_id", "==", this.$route.params.contato_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(elem => {
              elem.ref.update({
                  contato_id : this.contato_id,
                  primeiroNome: this.primeiroNome,
                  ultimoNome: this.ultimoNome,
                  morada: this.morada,
                  telemovel: this.telemovel
              })
              .then(() => {
                  this.$router.push({name: 'view-contato', params: {contato_id: this.contato_id}})
              })
          })
        })
    }
  }
};
</script>
<style>


</style>



