<template>
    <div>
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>{{primeiroNome}} {{ultimoNome}}</h4>
            </li>
            <li class="collection-item">Rua: {{morada}}</li>
            <li class="collection-item">Telemovel: {{telemovel}}</li>
        </ul>
        <router-link style="background-color: grey; color: white;" to="/cont" class="btn grey">Voltar</router-link>
        <button @click="apagarContato" class="btn red">Apagar</button>
        <div class="fixed-action-btn">
        <!-- botao com link para criação de novo contato -->
        <router-link v-bind:to="{name: 'editar-contato', params: {contato_id: contato_id}}" class="btn-floating">
          <i class="fa fa-pencil"></i>
        </router-link>
      </div>
    </div>
</template>

<script>
import firebase from "./firebaseInit";
export default {
  name: "ViewContato",
  data() {
    return {
      contato_id: null,
      primeiroNome: null,
      ultimoNome: null,
      morada: null,
      telemovel: null
    };
  },
  beforeRouteEnter(to, from, next) {
    firebase
      .collection("contatos")
      .where("contato_id", "==", to.params.contato_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(elem => {
          next(vm => {
            vm.contato_id = elem.data().contato_id;
            vm.primeiroNome = elem.data().primeiroNome;
            vm.ultimoNome = elem.data().ultimoNome;
            vm.morada = elem.data().morada;
            vm.telemovel = elem.data().telemovel;
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      firebase
        .collection("contatos")
        .where("contato_id", "==", this.$route.params.contato_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(elem => {
            this.contato_id = elem.data().contato_id;
            this.primeiroNome = elem.data().primeiroNome;
            this.ultimoNome = elem.data().ultimoNome;
            this.morada = elem.data().morada;
            this.telemovel = elem.data().telemovel;
            console.log(this.contato_id);
          });
        });
    },
    apagarContato() {
      if (confirm("Tem a certeza que deseja apagar o contacto?")) {
        firebase
          .collection("contatos")
          .where("contato_id", "==", this.$route.params.contato_id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(elem => {
              elem.ref.delete();
              this.$router.push("/cont");
            });
          });
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



