<template>
  <div id="app">
    <nav v-bind:class="active" v-on:click.prevent>
      <router-link to="/" class="home" v-on:click="makeActive('home')">Home</router-link>
      <router-link to="#" class="projects" v-on:click="makeActive('projects')">Projects</router-link>
      <router-link to="#"  class="services" v-on:click="makeActive('services')">Services</router-link>
      <router-link to="/cont" class="contact" v-on:click="makeActive('contact')">Contatos</router-link>
    </nav>    
    <div class="container">
      <router-view/>
    </div>
    <div class="small">
      <quick-menu  position="bottom-left" backgroundColor ="#ffcd00" :menu-count='4'
       :icon-class='["fa fa-address-book", "fa fa-home", "fa fa-line-chart", "fa fa-upload"]' 
       :menu-url-list='["#/cont","#/signin","#","#/"]'></quick-menu>
    </div>

  </div>
  <!-- background-color: #FFFCEC-->
</template>

<script>
import Navbar from "./components/Navbar";
import quickMenu from "vue-quick-menu";
export default {
  name: "App",
  data() {
    return {
      active: 'home'
    };
  },
  components: {
    Navbar,
    quickMenu
  },
  methods: {
    makeActive: function(item){
        data.active = item
    }
  }
};
</script>

<style>

*{
    margin:0;
    padding:0;
}

body{
    font:15px/1.3 'Open Sans', sans-serif;
    color: #5e5b64;
    text-align:center;
}

a, a:visited {
    outline:none;
    color:#389dc1;
}

a:hover{
    text-decoration:none;
}

section, footer, header, aside, nav{
    display: block;
}

/*-------------------------
    The menu
--------------------------*/

nav{
    display:inline-block;
    background-color:#5597b4;
    box-shadow:0 1px 1px #ccc;
    border-radius:2px;
}

nav a{
    display:inline-block;
    padding: 18px 30px;
    color:#fff !important;
    font-weight:bold;
    font-size:16px;
    text-decoration:none !important;
    line-height:1;
    text-transform: uppercase;
    background-color:transparent;

    -webkit-transition:background-color 0.25s;
    -moz-transition:background-color 0.25s;
    transition:background-color 0.25s;
}

nav a:first-child{
    border-radius:2px 0 0 2px;
}

nav a:last-child{
    border-radius:0 2px 2px 0;
}

nav.home .home,
nav.projects .projects,
nav.services .services,
nav.contact .contact{
    background-color:#ffcd00;
}

p{
    font-size:22px;
    font-weight:bold;
    color:#7d9098;
}

p b{
    color:#ffffff;
    display:inline-block;
    padding:5px 10px;
    background-color:#c4d7e0;
    border-radius:2px;
    text-transform:uppercase;
    font-size:18px;
}
.resource {
  margin: 20px 0;
}
</style>



